<template>
  <div class="wrap-g">
    <mt-header title="排行榜" fixed  style="background: #fff;color: #1B1B1B;">
    <router-link to="/" slot="left" >
      <mt-button icon="back"></mt-button>
    </router-link>
    <!--<mt-button icon="more" slot="right"></mt-button>-->
  </mt-header>

    <header style="top:40px;" class="rankNav wrap-g">
      <el-tabs v-model="activeName"  @tab-click="handleClick" style="font-size: 12px;" >
        <el-tab-pane label="视频课程" name="/videocourse" ></el-tab-pane>
        <el-tab-pane label="语音课程" name="/phoneticcourse"></el-tab-pane>
        <el-tab-pane label="直播课程" name="/livecours"></el-tab-pane>
        <el-tab-pane label="热门老师" name="/hotteacher"></el-tab-pane>
      </el-tabs>
    </header>
    <div class="main" style="margin-top: 40px;">
      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        activeName:"/videocourse",
      }
    },
    methods:{
      handleClick(e){
        this.$router.push({ path: e.name});
          console.log(e.name)
      },

    },
    mounted(){
        this.activeName=this.$route.path;
    },
  }
</script>


<style >

</style>



